.event {
  padding: 20px;
  margin-bottom: 10px;
  text-align: left;
  border-radius: 4px;
  position: relative;
}

.details-btn {
  position: absolute;
  right: 5px;
  bottom: 5px;
  padding: 5px 8px;
  border-radius: 4px;
  outline: none;
  cursor: pointer;
  background-color: #8f8cf2;
  color: white;
  border: none;
  transition: background-color 0.7s ease; /* Smooth color transition */
}

.details-btn:hover {
  background-color: #a653f5; /* Darken the color a bit on hover */
}
